import React from 'react';
import firebase from '../../Firebase';
import { Link } from 'react-router-dom';

class Show extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            blog: {},
            key: ''
        };
    }

    componentDidMount() {
        const ref = firebase.firestore().collection('blogs').doc(this.props.match.params.id);
        ref.get().then((doc) => {
            if (doc.exists) {
                this.setState({
                    blog: doc.data(),
                    key: doc.id,
                    isLoading: false
                });
            } else {
                console.log("No such document!")
            }
        });
    }

    delete(id) {
        firebase.firestore().collection('blogs').doc(id).delete().then(() => {
            console.log('Document succesfully deleted!');
            this.props.history.push('/blog')
        }).catch((error) => {
            console.log('Error removing document: ', error);
        })
    }

    render() {
        return (
            <div>
                <Link to={'/blog'}>Back to Blog</Link>
                <div>
                    <h3>{this.state.blog.title}</h3>
                    <dl>
                        <dt>Content</dt>
                        <dd>{this.state.blog.content}</dd>
                        <dt>Author: </dt>
                        <dd>{this.state.blog.author}</dd>
                    </dl>
                    <Link to={`/edit/${this.state.key}`}>Edit</Link>
                    <button onClick={this.delete.bind(this, this.state.key)}>Delete</button>
                </div>
            </div>
        );
    }
}

export default Show;